import React from "react"
import Styles from "./Info.module.css"

const Info = () => {
    const items = [
        { id: 1, name: "Требования к помещению, где находится больной", text: "В этой комнате пациент будет находиться большую часть своего времени, поэтому необходимо сделать все возможное, чтобы добиться оптимальных и психологически комфортных условий.В первую очередь, требуется позаботиться о хорошем освещении. Темнота негативно влияет на психоэмоциональное состояние больного.Очень важное обеспечить полное отсутствие постороннего шума, чтобы больному было действительно комфортно находиться в помещении. Также не забывайте о досуге. Если пациент любит смотреть телевизор, обязательно поставьте в комнату технику.И, конечно же, самое главное требование - обеспечение циркуляции воздуха. Нужно ежедневно проветривать помещение, в котором находится тяжелобольной, хотя бы один раз в день. При возможности, стоит регулярно делать кварцевание, чтобы уничтожить патогенные микроорганизмы." },
        { id: 2, name: "Температурный режим", text: "Нужно отрегулировать температуру в помещении таким образом, чтобы больному не было ни жарко, ни холодно. Излишняя потливость приводит к негативным изменениям кожных покровов, а чрезмерный холод - к ослабеванию иммунитета. Оптимальная температура - от 18 до 22 градусов Цельсия. В зимнее время года больного необходимо накрывать теплым одеялом." },
        { id: 3, name: "Обеспечение смены постельного белья", text: "Согласно эпидемиологическим рекомендациям, перестилать постель тяжелобольного следует не менее чем один раз в 2 суток. Однако, при возможности, необходимо стараться делать это каждый день. Особенно это правило касается тех случаев, если во время осмотра тела больного были обнаружены пролежни. При несвоевременной смене постельного белья скапливающиеся в ткани патогенные бактерии могут усугубить ситуацию.Также стоит с осторожностью кормить больного в постели, чтобы не допустить загрязнения белья остатками пищи и крошками. Если у пациента случилась неконтролируемая дефекация, перестелить кровать следует немедленно." },
        { id: 4, name: "Перемещение тяжелобольного", text: "Важно понимать, что вследствие общего ослабления организма на фоне заболевания, кости пациента довольно хрупки. Чрезмерные усилия, грубые хватания могут только навредить. Обеспечьте плавность движений при перемещении тяжелобольного с места на место.Если предстоит дальняя поездка, то необходимо позаботиться о наличии в транспортном средстве специально отведенных мест, а также перевозить пациента в каталках." },
        { id: 5, name: "Расстановка мебели", text: "Если тяжелобольной может передвигаться самостоятельно, то необходимо позаботиться о том, чтобы он мог иметь свободный доступ ко всем предметам в комнате. Кроме того, для удобства ухода стоит обеспечить подход к кровати пациента с обеих сторон." },
        { id: 6, name: "Обеспечение надлежащего уровня гигиены", text: "Отсутствие гигиенических процедур может привести к возникновению новых патологий. Однако ухаживать за собой тяжелобольной часто просто не в состоянии. Необходимо делать это за него.Чистить зубы пациенту необходимо 2 раза в день: утром и вечером. После каждого принятия пищи рекомендуется ополаскивать ротовую полость дезинфицирующими растворами.После опорожнения больным кишечника нужно каждый раз подмывать кожу. Для этого нужно пользоваться медицинским судном. Также необходимо позаботиться о надлежащей гигиене интимных зон.Ежедневно тело пациента должно обтираться влажными салфетками. Также не забывайте при этом использовать дополнительные средства по уходу. При повышенном потоотделении, частоту обтираний следует увеличить.Мыть голову больному необходимо 1 раз в четыре дня или же по мере загрязнения. Не забывайте также следить за длиной и чистотой ногтей. Мужчинам нужно регулярно обстригать бороду." },
        { id: 7, name: "Кормление тяжелобольного", text: "Нередко тяжелобольной человек не может питаться самостоятельно. В таком случае следует помочь ему.Кормить пациента необходимо строго в положении полусидя, ни в коем случае не лежачем. Если не соблюдать это правило, существует большая вероятность того, что больной подавится едой. Кормить важно маленькими порциями мягкой пищи. Также стоит не забывать проверять температуру еды, чтобы она была не слишком горячей или холодной.Обязательно спрашивайте о сытости. Вследствие чрезмерно переполненного желудка может случиться рвота. Если у тяжелобольного отсутствует аппетит, все равно стоит выдавать пищи в количестве, предписанном лечащим врачом." },
        { id: 8, name: "Положение пациента в кровати", text: "Если у пациента отсутствует возможности передвигаться самостоятельно, делать это придется ухаживающим. Регулярное изменение положения тела - залог отсутствия пролежней.Рекомендуется изменять положение тела тяжелобольного в кровати каждые 2 часа. Это поможет улучшить процессы циркуляции крови. Если у пациента наблюдаются серьезные патологии питания тканей, то требуется увеличить частоту переворачиваний.Изменять положение тела нужно крайне аккуратно, грубое воздействие приведет к травмам. Во время переворачивания категорически запрещено поддерживать тяжелобольного за конечности. Лучше держать руки на плечах и бедрах пациента. Это необходимо для снижения вероятности вывиха конечностей.Стоит обязательно фиксировать положение тела с помощью специальных валиков и подушек. Благодаря воздействию воздуха существенно снижается риск возникновения опрелостей." },
        { id: 9, name: "Контроль психологического состояния", text: "Апатия, стресс и депрессия - частые спутники тяжелобольных людей. Ведь они начинают чувствовать себя обузой для близких. Очень важно постоянно контролировать психологическое состояние пациента.Лучше всего, если этим будет заниматься специалист. Если же вы решили делать все самостоятельно, то всегда в мягкой форме интересуйтесь состоянием больного.При общении очень важно уметь подбирать правильные слова, чтобы психологически не травмировать пациента. Ни в коем случае не говорите фраз на подобии «Я понимаю, что ты чувствуешь», «Все будет хорошо». Так родственники только усугубляют и без того критическое состояние. Ведь, по факту, они не могут понять, что в действительности чувствует тяжелобольной." }
    ]

    const elems = items.map(({id, name, text}) => {
        return(
            <div key={ id } className={Styles.item}>
                <h3 className={Styles.subheading}>{ name }</h3>
                <p className={Styles.text}>{ text }</p>
            </div>
        )
    })

    return(
        <div className={Styles.block}>
            <h2 className={Styles.heading}>Рекомендации по уходу</h2>
            { elems }
        </div>
    )
}

export default Info